ition: absolute;\r\n}\r\n\r\n.app-header {\r\n  display: flex;\r\n  flex-direction: column;\r\n  overflow-y: hidden;\r\n  cursor: move;\r\n}\r\n\r\n/* App Header */\r\n\r\n.heading {\r\n  padding: 15px 10px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  border-bottom: 1px solid var(--seperator_color);\r\n  user-select: none;\r\n}\r\n\r\n.heading p {\r\n  font-size: 1.25rem;\r\n  color: var(--text_color);\r\n  text-shadow: -1px 0 5px #d1cece;\r\n}\r\n\r\n.heading i {\r\n  background: url(../assets/drag-icon.png) no-repeat center center/cover;\r\n  width: 16px;\r\n  height: 16px;\r\n  filter: invert(0.35);\r\n}\r\n\r\n.add-item {\r\n  padding: 15px 10px;\r\n  border-bottom: 1px solid var(--seperator_color);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.add-item input {\r\n  border: none;\r\n  cursor: default;\r\n  font-size: 1.2rem;\r\n  font-style: italic;\r\n  flex: 1;\r\n}\r\n\r\n.task input {\r\n  font-family: sans-serif, monospace;\r\n  border: none;\r\n  cursor: default;\r\n  text-shadow: -1px 0 5px #d1cece;\r\n}\r\n\r\n.add-item input:focus {\r\n  outline: none;\r\n}\r\n\r\n.add-item i {\r\n  background: url(../assets/return-icon.png) no-repeat center center;\r\n  width: 7px;\r\n  height: 12px;\r\n  padding: 10px 12px;\r\n  cursor: pointer;\r\n}\r\n\r\n.add-item i:active {\r\n  filter: invert(0.7);\r\n}\r\n\r\n/* The List */\r\n\r\n.to-do-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  overflow-y: scroll;\r\n}\r\n\r\n.list-item {\r\n  padding: 15px 10px;\r\n  font-size: 1.1rem;\r\n  color: var(--text_color);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  border-bottom: 1px solid var(--seperator_color);\r\n}\r\n\r\n.list-item:active {\r\n  cursor: grabbing;\r\n  cursor: -moz-grabbing;\r\n  cursor: -webkit-grabbing;\r\n}\r\n\r\n.task {\r\n  flex: 1;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.task-description {\r\n  flex: 1;\r\n}\r\n\r\n.completed .task-description {\r\n  text-decoration: line-through;\r\n  color: var(--text_color_completed);\r\n}\r\n\r\n.task input:focus {\r\n  outline: none;\r\n}\r\n\r\n.list-item .drag-btn {\r\n  display: block;\r\n  background: url(../assets/verticle-menu.png) no-repeat center center/cover;\r\n  height: 20px;\r\n  width: 20px;\r\n  filter: invert(0.5);\r\n  cursor: move;\r\n}\r\n\r\n.list-item .del-btn {\r\n  background: url(../assets/delete.png) no-repeat center center/cover;\r\n  height: 20px;\r\n  width: 20px;\r\n  filter: invert(0.5);\r\n  cursor: pointer;\r\n}\r\n\r\n.del-btn:hover {\r\n  filter: invert(0.3);\r\n}\r\n\r\n/* App Footer */\r\n\r\n.app-footer {\r\n  padding: 15px 10px;\r\n  text-align: center;\r\n  background-color: #f6f6f6;\r\n}\r\n\r\n.app-footer a {\r\n  text-decoration: none;\r\n  color: #777;\r\n}\r\n\r\n.app-footer a:hover {\r\n  color: var(--text_color);\r\n}\r\n\r\n/* Utility Class */\r\n\r\n.field-focus {\r\n  background-color: var(--field_focus);\r\n}\r\n"],"sourceRoot":""}]);
